.Navlist li {
  list-style-type: none;
  display: "inline";
}

.nav {
  float: left;
  text-align: center;
  font-family: Verdana, Geneva, sans-serif;
  letter-spacing: 1px;
}

.link {
  display: block;
  color: rgb(245, 245, 245);
  text-align: center;
  padding: 20px 24px;
  font-family: Verdana, Geneva, sans-serif;
  letter-spacing: 1px;
  text-decoration: none;
}

.link:hover {
  color: white;
  background-color: rgb(12, 12, 12);
}
.right {
  align-content: center;
  top: 0;
  right: -2em;
  padding-left: 10px;
  margin-left: 10px;
  background-color: rgb(104, 104, 104);
}
.left::-webkit-scrollbar {
  display: none;
}
.left {
  align-content: center;
  float: left;
  padding: 1em;
  left: 0;
  box-sizing: content-box;
}

.column {
  height: 100%;
  width: 50%;
  position: fixed;
  z-index: 1;
  overflow-x: hidden;
}
#map {
  height: 400px;
  width: 100%;
}
#pagelink {
  display: inline;
  color: rgb(238, 229, 221);
  font-family: Arial, sans-serif;
  letter-spacing: 1px;
  line-height: 26px;
  font-size: 16px;
  font-weight: bold;
}
#selected {
  color: white;
  padding: 20px 26px;
  background-color: rgb(65, 0, 11);
}

#navbar {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  text-align: center;
  background-color: rgb(138, 13, 55);
  width: 60%;
}

#homenav {
  list-style-type: none;
}

#list {
  list-style-type: square;
  color: rgb(238, 229, 221);
  font-family: Arial, sans-serif;
  letter-spacing: 3px;
  padding: 10px;
  margin: 10px;
  line-height: 26px;
  font-weight: bold;
}

.filler_img {
  width: 90%;
  height: auto;
  align-content: center;
  padding: 10px;
  margin: 10px;
}
.img_text {
  padding: 10px;
  margin: 10px;
}

h1 {
  display: block;
  font-family: Arial, sans-serif;
  letter-spacing: 1px;
  color: rgb(238, 229, 221);
}

h2 {
  display: block;
  font-family: Arial, sans-serif;
  letter-spacing: 1px;
  color: rgb(238, 229, 221);
}

body {
  background-color: rgb(22, 22, 22);
}

p {
  display: block;
  color: rgb(3, 3, 3);
  font-family: Arial, sans-serif;
  letter-spacing: 1px;
  line-height: 33px;
  font-size: 16px;
  font-weight: bold;
  overflow: hidden;
}
